  </div>
</template>

<script>
  export default{
    name:'OrderList',
    data(){
      return{
        tab:1
      }
    },
    created(){
      let { id } = this.$route.params;
      console.log(this.$route)
      console.log(this.$route.params)
      console.log(id)
      if(id != null){
        this.tab=id;
      }
    }

