<style scoped>
  .orderlist{
    height: 100vh;
    background-color: #f4f4f4;
    padding: 40px 0 0;
  }
  .order_bars{
    width: 100%;
    padding: 12px 10px;
    display: flex;
    /* 换行，第一行在上方 */
    flex-wrap: wrap;
    /* 间隔相等 ，自动分配距离*/
    justify-content: space-around;
    align-content: flex-start;
    position: fixed;
    top: 40px;
    left: 0;
    background-color: #f4f4f4;
  }
  .order_bars p{
    text-align: center;
    flex-grow: 1;
  }
  .order_bars .active{
    color: rgb(4, 68, 207);
  }
  .order{
    width: 100%;
    height: 100%;
    /* background-color: blue; */
    padding: 41px 10px;
    overflow: auto;
    

  }

  .order_all{
    background-color: white;
    border-radius: 10px;
    padding: 12px;
    margin-bottom: 10px;
