  }
  .order_right{
    flex-basis: 66%;
    flex-grow: 1;
    /* background-color: tomato; */
    /* position: relative; */

  }
  .order_right P{
    /* background-color: turquoise; */
    font-size: 0.875rem;
    padding: 0 8px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    text-overflow: ellipsis;
    overflow: hidden;
    /* position: absolute; */
    /* top: 0px; */
  }
  .order_price{
    flex-basis: 10%;
    flex-grow: 1;
    text-align: right;
  }
  .fukuan{
    text-align: right;
    padding: 5px 10px;
  }
  .order_bottom{
    margin-top: 8px;
    text-align: right;
  }
  .order_bottom span{
    padding: 5px 15px;
    border: 1px solid #ddd;
    border-radius: 50px;
    font-size: 0.875rem;
    color: #666;
  }
  .order_bottom p{
    color: rgb(255, 129, 71);
    float: left;
  }
</style>



